import React, { forwardRef } from 'react';
import { node, oneOf, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-tab';

const TabListStateless = forwardRef(({
  children,
  className,
  orientation,
  role,
  ...remainingProps
}, ref) => (
  <ul
    className={classnames(`${CSS_PREFIX}-list`, {
      [`${CSS_PREFIX}--${orientation}`]: Boolean(orientation)
    }, className)}
    ref={ref}
    role={role}
    {...remainingProps}
  >
    {children}
  </ul>
));

TabListStateless.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
   * Orientation
   */
  orientation: oneOf(['vertical']),

  /**
   * @ignore
   */
  role: string
};

TabListStateless.defaultProps = {
  role: 'tablist'
};

TabListStateless.displayName = 'TabListStateless';

export default TabListStateless;
